body {
  margin: 0;
  background: #ff46f6;
  color:white;
  background-image: url('https://www.paris21.org/themes/custom/partnership/images/backgrounds/news-background-219-w1900.jpg');
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.container {
  width: 90%;
  margin: 0 auto;
  max-width: 1200px;
  padding: 20px;
}

h1 {
  text-align: center;
  color: #79afff;
   font-weight: 700;
}

form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  background-color: #fff;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.form-group {
  display: flex;
  flex-direction: column;
}

input, textarea {
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 20px;
  width: 100%;
  font-weight: bold;
  margin-top: 5px;
  max-width: 100%;
  box-sizing: border-box;
  transition: border-color 0.3s;
}

input:focus, textarea:focus {
  border-color: #4a90e2;
  outline: none;
}

textarea {
  resize: vertical;
}

.form-actions {
  display: flex;
  gap: 10px;
  justify-content: center;
}
.btn {
  padding: 12px 25px;
  background-color: #000000;
  color: #fff;
  border: none;
  cursor: pointer;margin: 0 5px;
  font-size: 14px;
  font-weight: bold;
  border: 1px solid rgb(215, 255, 39);
  transition: background-color 0.3s;
}

.btn-edit {
  padding: 12px 25px;
  background-color: #4a90e2;
  color: #fff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin: 0 5px;
  font-size: 12px;
  transition: background-color 0.3s;
}
.btn-delete {
  padding: 12px 25px;
  background-color: #4a90e2;
  color: #fff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 12px;
  transition: background-color 0.3s;
}
.btn-toggle {
  padding: 12px 15px;
  background-color: #4a90e2;
  color: #fff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin: 5px;
  font-size: 12px;
  transition: background-color 0.3s;
}

.btn:hover {
  background-color: #357ab8;
}

.btn-delete {
  background-color: #e74c3c;
}

.btn-delete:hover {
  background-color: #c0392b;
}

.btn-toggle {
  background-color: #f39c12;
}

.btn-toggle:hover {
  background-color: #d35400;
}

.task-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
}

.task {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  flex: 1 1 calc(44.222% - 20px);
  box-sizing: border-box;
  transition: transform 0.3s, box-shadow 0.3s;
}

.task.completed {
  border-left: 5px solid #2ecc71;
}

.task.incomplete {
  border-left: 5px solid #e74c3c;
}

.task:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
}

.task h3 {
  color: #4c58ff;
  margin-top: 0;
}

.task p {
  margin: 0;
  color: #666;
}

.task button {
  margin-top: 10px;
}

@media (max-width: 768px) {
  .task {
      flex: 1 1 calc(50% - 20px);
  }
}

@media (max-width: 480px) {
  .task {
      flex: 1 1 100%;
  }
}


.task-status {
  padding: 5px 10px;
  color: #fff;
  font-weight: bold;
  text-align: center;
  margin-top: 20px;
}

.task-status.completed {
  background-color: #2ecc71;
}

.task-status.incomplete {
  background-color: #e74c3c;
}
p {
  font-size: 18px;
  padding: 6px;
}
.green {
  color: rgb(255, 137, 27);
  font-size: 18px;
  margin-top: 4px;
}
.yellow {
  color: rgb(0, 231, 12);
  font-size: 18px;
}
.violet {
  color: violet;
  font-size: 18px;
}